                "productName": i % 2 === 0 ? "" : "",
                "quantity": 10 + i,
                "weight": 300 + i * 20,
                "volume": 10 + i * 0.8,
                "packageType": i % 3 === 0 ? "" : (i % 3 === 1 ? "" : ""),
                "receiverName": `${i}`,
                "receiverPhone": `137${i}${i}${i}${i}${i}${i}${i}${i}`,
                "deliveryAddress": `${i % 3 === 0 ? "Sacramento" : (i % 3 === 1 ? "Oakland" : "Riverside")}${i*10}`,
                "deliveryStatus": i % 4 === 0 ? "" : (i % 4 === 1 ? "" : (i % 4 === 2 ? "" : "")),
                "shippingMark": `SHIP-${1000 + i}-A`,
                "remarks": i % 2 === 0 ? "" : ""
              }
            ]
          });
        }
        
        fetchData();
      });
      
      // 
      const fetchData = () => {
        loading.value = true;
        try {
          // JSON
          totalItems.value = tableData.value.length;
          applyFiltersAndPagination();
          
          // 
          const pending = tableData.value.filter(item => item.appointmentStatus === "").length;
          const processing = tableData.value.filter(item => item.appointmentStatus === "" || item.appointmentStatus === "").length;
          const completed = tableData.value.filter(item => item.appointmentStatus === "").length;
          const cancelled = tableData.value.filter(item => item.appointmentStatus === "").length;
          
          // 
          const statusViews = document.querySelectorAll('.status-view-group .el-radio-button');
          if (statusViews.length >= 5) {
            statusViews[0].querySelector('.el-radio-button__inner').textContent = `(${tableData.value.length})`;
            statusViews[1].querySelector('.el-radio-button__inner').textContent = `(${pending})`;
            statusViews[2].querySelector('.el-radio-button__inner').textContent = `(${processing})`;
            statusViews[3].querySelector('.el-radio-button__inner').textContent = `(${completed})`;
            statusViews[4].querySelector('.el-radio-button__inner').textContent = `(${cancelled})`;
          }
        } catch (error) {
          console.error(':', error);
        } finally {
          loading.value = false;
        }
      };
      
      // 
      const applyFiltersAndPagination = () => {
        let filteredData = [...tableData.value];
        
        //  - 
        if (['pending', 'processing', 'completed', 'cancelled'].includes(currentView.value)) {
          const statusMap = {
            'pending': '',
            'processing': '',
            'completed': '',
            'cancelled': ''
          };
          
          const targetStatus = statusMap[currentView.value];
          if (targetStatus) {
            filteredData = filteredData.filter(item => item.loadingStatus === targetStatus);
          }
        }
        
        // 
        if (searchText.value) {
          filteredData = filteredData.filter(item => {
            const value = item[searchType.value];
            if (!value) return false;
            return value.toString().toLowerCase().includes(searchText.value.toLowerCase());
          });
        }
        
        // 
        if (dateRange.value && dateRange.value.length === 2) {
          const fieldMap = {
            'create': 'createdAt',
            'issue': 'issueDate',
            'appointment': 'loadingAppointmentTime'
          };
          
          const dateField = fieldMap[createDateType.value];
          const [startDate, endDate] = dateRange.value;
          
          filteredData = filteredData.filter(item => {
            const itemDate = item[dateField];
            if (!itemDate) return false;
            
            const dateOnly = itemDate.split(' ')[0];
            return dateOnly >= startDate && dateOnly <= endDate;
          });
        }
        
        // 
        if (activeFilters.value.length > 0) {
          filteredData = filteredData.filter(item => {
            const filterResults = activeFilters.value.map(filter => {
              const { field, operator, value } = filter;
              const itemValue = item[field];
              
              if (itemValue == null) {
                return false;
              }
              
              switch (operator) {
                case 'eq':
                  return itemValue == value;
                case 'neq':
                  return itemValue != value;
                case 'contains':
                  return itemValue.toString().toLowerCase().includes(value.toString().toLowerCase());
                case 'notContains':
                  return !itemValue.toString().toLowerCase().includes(value.toString().toLowerCase());
                case 'startsWith':
                  return itemValue.toString().toLowerCase().startsWith(value.toString().toLowerCase());
                case 'endsWith':
                  return itemValue.toString().toLowerCase().endsWith(value.toString().toLowerCase());
                case 'gt':
                  return Number(itemValue) > Number(value);
                case 'gte':
                  return Number(itemValue) >= Number(value);
                case 'lt':
                  return Number(itemValue) < Number(value);
                case 'lte':
                  return Number(itemValue) <= Number(value);
                default:
                  return true;
              }
            });
            
            return filterLogic.value === 'and'
              ? filterResults.every(r => r)
              : filterResults.some(r => r);
          });
        }
        
        // 
        if (groupField.value) {
          filteredData = groupData(filteredData, groupField.value);
        }
        
        // 
        totalItems.value = filteredData.length;
        
        // 
        const start = (currentPage.value - 1) * pageSize.value;
        const end = start + pageSize.value;
        displayData.value = filteredData.slice(start, end);
      };
      
      // 
      const groupData = (data, field) => {
        const groups = {};
        
        data.forEach(item => {
          // undefinednull""
          const fieldValue = item[field];
          const value = fieldValue === undefined || fieldValue === null ? '' : String(fieldValue);
          
          if (!groups[value]) {
            groups[value] = [];
          }
          groups[value].push({...item});
        });
        
        const result = [];
        for (const [key, items] of Object.entries(groups)) {
          result.push({
            id: `group-${key}`,
            groupName: key,
            count: items.length,
            isGroup: true,
            groupLevel: 0,
            children: items
          });
        }
        
        return result;
      };
      
      // 
      const handleSelectionChange = (rows) => {
