      // 
      const handleDetail = (row) => {
        console.log('', row);
        // 
      };
      
      // 
      const handleCommand = (command, row) => {
        currentRow.value = row;
        let addressInfo = ''; // 
        switch (command) {
          case 'memo':
            memoDialogVisible.value = true;
            memoForm.value.memo = row.remarks || '';
            break;
          case 'followUp':
