            // 
            loadHistoryRecords(row);
            break;
          case 'palletDetail':
            // 
            ElMessage.info('');
            break;
          case 'copyAddress':
            // 
            addressInfo = `${row.address || ''}, ${row.city || ''}, ${row.state || ''} ${row.zipCode || ''}`;
            navigator.clipboard.writeText(addressInfo)
              .then(() => {
                ElMessage.success('');
              })
              .catch(err => {
                console.error(':', err);
                ElMessage.error('');
              });
            break;
          case 'markException':
            // 
            handleMarkException(row);
            break;
          case 'detail':
            // 
            handleDetail(row);
            break;
          case 'expense':
            //  - 
            // ElMessage.info('');
            router.push({ name: 'FBXExpenseDetails', params: { id: row.id } }); //  router 
            break;
          case 'uploadPod':
            handleUploadPod(row);
            break;
          default:
            break;
        }
      };
  
      // 
      const showFilterPanel = () => {
        filterPanelVisible.value = true;
      };
  
      // 
      const showAddTrainDialog = () => {
        // 
        console.log('showAddTrainDialog');
      };
  
      // /
      const handleExpandChange = (row, expanded) => {
        console.log('/:', row.id, expanded ? '' : '');
        // expandRowKeysElement Plus
      };
  
      // 
      const handleSortChange = ({ prop, order }) => {
        if (!prop || !order) {
          // 
          applyFiltersAndPagination();
          return;
        }
        
        // 
        const sortedData = [...displayData.value];
        
        // 
        sortedData.sort((a, b) => {
          let aValue = a[prop];
          let bValue = b[prop];
          
          // 
          if (!isNaN(aValue) && !isNaN(bValue)) {
            aValue = Number(aValue);
            bValue = Number(bValue);
          }
          
          // 
          if (typeof aValue === 'string' && typeof bValue === 'string') {
            if (order === 'ascending') {
              return aValue.localeCompare(bValue);
            } else {
              return bValue.localeCompare(aValue);
            }
          }
          
          // 
          if (order === 'ascending') {
            return aValue > bValue ? 1 : -1;
          } else {
            return aValue < bValue ? 1 : -1;
          }
        });
        
        displayData.value = sortedData;
      };
  
      // 
      const getGroupColumnLabel = (prop) => {
